import React, { useState, useEffect } from "react";
import Header from "./conponent/parts/Header";
import "./App.css";

const App: React.FC = (props: any) => {
  const [posts, setPosts] = useState([
    { month: String, name: String, color: String },
  ]);
  useEffect(() => {
    fetch("http://localhost:8090/api/sample/getBirthStone", { method: "GET" })
      .then((res) => res.json())
      .then((data) => {
        setPosts(data);
      });
  }, []);

  return (
    <div className="App">
      <Header />
      <p>app</p>
      <div>
        <ul>
          {posts.map((post) => (
            <p>
              name:{post.name} month:{post.month} color:{post.color}
            </p>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default App;
